.btn-class {
  display: inline-block;
  border: 6px solid black;
  
  color: #1f063b;
  border-radius: 0px 0px 50px 50px;
  -webkit-border-radius: 0px 0px 50px 50px;
  -moz-border-radius: 0px 0px 50px 50px;
  font-family: roboto;
  width: auto;
  height: 90px;
  font-size: 30px;
  padding: 8px 46px;
  box-shadow: inset -1px 1px 0 0 #fff6ce;
  -moz-box-shadow: inset -1px 1px 0 0 #fff6ce;
  -webkit-box-shadow: inset -1px 1px 0 0 #fff6ce;
  text-shadow: 2px 0px 0 #ffffff;
  background-image: linear-gradient(to top, #fce374, #fcdf5b);
  background-color: #fce374;
  font-weight: 700;
  margin-bottom: 60px;
  margin-top: 35px;
}
.btn-class:hover,
.btn-class:active {
  /*border:6px solid #967D09;*/
  color: #705d07;
  box-shadow: 0px 22px 48px 26px #fff6ce, 0px 35px 44px 0 #e3c852,
    0 31px 37px 17px #fce88d, inset 0px 4px 50px 5px #ffffff;
  -moz-box-shadow: 0px 22px 48px 26px #fff6ce, 0px 35px 44px 0 #e3c852,
    0 31px 37px 17px #fce88d, inset 0px 4px 50px 5px #ffffff;
  -webkit-box-shadow: 0px 22px 48px 26px #e0a1aa, 0px 35px 44px 0 #d8166a,
    0 31px 37px 17px #7eb4e3, inset 0px 4px 50px 5px #ffffff;
  background-color: #bfc332;
}

